@import: default * {
}
* {
  transition: 0.1s;
}
.App {
  text-align: left;
  width: 100vw;
  height: calc(100vh);
  padding-top: 10px;
  background-color: var(--bg);
  box-sizing: border-box;
  padding-top: 40px;
  color: var(--main-text);
}
#search-area {
  height: 40px;
  width: 40%;
  margin: auto;
  margin-bottom: 30px;
}
#search-area > * {
  height: 100%;
}
#search-area input[type="text"] {
  width: 70%;
}
#search-area input[type="submit"] {
  width: 20%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
#weather-area {
  width: 60%;
  min-width: 100px;
  margin: auto;
  margin-top: 10px;
  border-radius: 50px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 10px;
  background-color: var(--compBg);
  box-shadow: 0px 0px 10px var(--border);
}
#image-area,
#info-area {
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
}
#row-first,
#row-second,
#row-third {
  display: flex;
  width: 100%;
  align-self: center;
  flex-grow: 1;
  font-size: 2vw;
}
#wrapper {
  width: 100%;
  margin: auto;
  margin-left: 0;
}
#image-area {
  align-items: center;
  justify-items: center;
  text-align: center;
  padding-left: 10%;
}
img {
  width: 80%;
}
h1 {
  color: var(--title);
}
.sunny {
  --compBg: rgb(143, 177, 233, 0.5);
  --title: #ff9d76;
  --bg: #0669bf;
  --main-text: black;
  --border: silver;
}
.rain {
  --compBg: rgb(115, 169, 217, 0.5);
  --title: #2bb9d9;
  --bg: #23518c;
  --main-text: black;
  --border: silver;
}
.cloudy {
  --compBg: rgb(63, 78, 166, 0.5);
  --title: #5da684;
  --bg: #2e3440;
  --main-text: black;
  --border: silver;
}
.other-weather {
  --compBg: rgb(74, 14, 115, 0.5);
  --title: #f2a341;
  --bg: #102040;
  --main-text: white;
  --border: #670aa6;
}
